import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import { PhotosContainer } from './styles'

import CardPhoto from '../CardPhoto'

export default function Photos() {
	const photosData = useStaticQuery(graphql`
		query {
			allInstaNode {
				edges {
					node {
						id
						likes
						comments
						mediaType
						preview
						original
						timestamp
						caption
						localFile {
							childImageSharp {
								fixed(width: 150, height: 150) {
									...GatsbyImageSharpFixed
								}
							}
						}
						# Only available with the public api scraper
						thumbnails {
							src
							config_width
							config_height
						}
						dimensions {
							height
							width
						}
					}
				}
			}
		}
	`)
	return (
		<PhotosContainer name="galery">
			<h1>Galeria</h1>
			<p>Abaixo você pode conferir alguns dos meus melhores cliques</p>
			<a href="https://instagram.com/yagoernandes">
				Clique aqui para ver todos eles!
			</a>
			<br />
			<br />
			<div className="photos">
				{photosData.allInstaNode.edges.slice(0, 6).map(photo => (
					<CardPhoto
						key={photo.node.id}
						url={photo.node.preview}
						link={`https://www.instagram.com/p/${photo.node.id}/`}
					/>
				))}
			</div>
		</PhotosContainer>
	)
}
